.contact-icon-lin {
    background-color: #0EA86C;
    color: #B8F4BB;
    border-radius: 50%;
    font-size: 90px;
    position: absolute;
    top: 0;
    right: 0;
    filter: drop-shadow(30px 10px 4px rgba(0, 0, 0, .80));
    height: 90px;
}

.contact-icon-lin:hover {
    color: white;
}

.contact-icon-git {
    background-color: #0EA86C;
    color: #B8F4BB;
    border-radius: 50%;
    font-size: 90px;
    position: absolute;
    top: 0;
    left: 0;
    filter: drop-shadow(30px 10px 4px rgba(0, 0, 0, .80));
    height: 90px;
}

.contact-icon-git:hover {
    color: white;
}

.robot-container {
    width: 500px;
    height: 500px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}