.container {
  width: 100vw;
  height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.icon-small {
  font-size: 60px;
  margin: 20px;
  background-color: #A1CF6B;
  width: 100px;
  height: 100px;
  border-radius: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.icon-small:hover {
  color: white
}

.icon-small-outline {
  position: static;
  top: -20px;
  left: -20px;
  right: -20px;
  bottom: -20px;
  border: 10px solid #72C738;
  border-radius: 50%;
  z-index: 0;
  background-color: lightblue;
}

.icon-title {
  height: 100px;
  width: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.icon-row {
  display: flex;
  flex-direction: row;
}