.container-header {
    color: #B8F4BB;
    background-color: #0EA86C;
    height: 10vh;
    width: 100vw;
    font-size: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.container-header-title {
    margin-left: 20px;
}

.container-header-links > * {
    font-size: 30px;
    margin-right: 20px;
}

.header-links-active {
    color: white;
    text-decoration: underline;
}